.App {
  max-width: 1200px;
  margin: auto;
  margin-bottom: 3rem;
}

.AppTab {
  margin: 2rem;
  background-color: white;
  height: max-content;
  border-radius: 1rem;
}

.Title {
  display: flex;
  margin: 1rem;
  margin-right: 2rem;
  margin-left: 2rem;
}

.App-header {
  min-height: 3rem;
  max-width: 30rem;
  align-items: left;
  justify-content: left;
  font-size: 1.5rem;
  color: black;
  display: inline;
  float: left;
}

.App-link {
  color: #61dafb;
}

.LanguageSelector {
  min-height: 2rem;
  min-width: 5rem;
  max-width: 10rem;
  display: inline;
  float: right;
  margin-top: auto;
  margin-left: auto;
  margin-right: 2rem;
}

.warning {
  font-size: 0.8rem;
  color: gray;
}

p {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.Align-center {
  text-align: center;
  align-items: center;
  justify-content: center;
}

.Invisible {
  font-size: 1rem;
  color: rgba(255, 255, 255, 0);
  display: block;
}

.Bold {
  font-weight: bold;
}

.MinWidth {
  min-width: 7rem;
}

.MaxWidth {
  max-width: 20%;
}

@media screen and (max-width: 1080px) {
 
  .MinWidth {
    min-width: 45%;
  }

  .MaxWidth {
    padding-left: 2rem;
    padding-right: 2rem;
    max-width: 15%;
  }

}

@media screen and (max-width: 480px) {
 
  .MinWidth {
    min-width: 93%;
  }

  .MaxWidth {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    max-width: 15%;
  }

}